<template>
  <div class="mt-10 md:w-1/2 lg:w-1/3 mx-2 md:mx-auto poppins">
    <div
      :class="login_alert_variant"
      class="text-white text-center font-bold p-5 mb-4"
      v-if="login_show_alert"
    >
      {{ login_alert_msg }}
    </div>
    <vee-form :validation-schema="schema" @submit="login">
      <!-- Email -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white" autocomplete="off"
          >Phone Number</label
        >
        <vee-field
          type="tel"
          name="phone"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="e.g 01612345678"
        />
        <ErrorMessage class="text-red-600" name="phone" />
      </div>

      <button
        type="submit"
        class="
          block
          w-full
          bg-red-500
          text-white
          py-1.5
          px-3
          rounded
          transition
          hover:bg-red-600
        "
        :disabled="login_in_submission"
      >
        Login
      </button>
    </vee-form>
  </div>
</template>

<script>
export default {
  name: "LoginFrom",
  beforeRouteEnter(to, from, next) {
    let user = localStorage.getItem("ssrb");
    if (user == undefined) {
      next();
      return;
    }
    next({
      path: "/",
    });
  },
  data() {
    return {
      schema: {
        phone: "required|min:10|max:13",
      },

      login_in_submission: false,
      login_show_alert: false,
      login_alert_variant: "bg-blue-500",
      login_alert_msg: "Please wait! Untill your login in progress.",
    };
  },
  methods: {
    async login(values) {
      this.login_show_alert = true;
      this.login_in_submission = true;
      this.login_alert_variant = "bg-blue-500";
      this.login_alert_msg =
        "Please wait! Untill your login has been completed";
      let pl = values.phone.length;
      let phone = values.phone.substring(pl - 10, pl);
      try {
        await this.$store.dispatch("login", {
          phone,
          router: this.$router,
        });
      } catch (err) {
        this.login_in_submission = false;
        this.login_alert_variant = "bg-red-500";
        this.login_alert_msg =
          "An Unexpected error occurred. Please try again later.";
        return;
      }

      this.login_alert_variant = "bg-green-500";
      this.login_alert_msg = "Success! Your account has been logged in.";
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
